      所属行业：<select v-model="form.trade" id="trade" style="width:100px;height:40px;">
                  <option value="education">教育</option>
                  <option value="finance">金融</option>
              </select><br>
      职位：  <input type="text" name="" id="position"  placeholder="必填" v-model="form.position"/>
      公司地址全称：<input type="text" name="" id="address3"  placeholder="必填" v-model="form.address3"/>
      公司类型：<input type="text" name="" id="company_type"  placeholder="必填" v-model="form.company_type"/><br>
