<script>
import {LoanInput} from "@/utils/api"

export default {
  data() {
    return {
      form: {
         name: "",
         identity_card: "",
         birthday: "",
         sex: "woman",
         marriage: "unmarried",
         education: "college",
         address1: "",
         address2: "",
         phone: "",
         mobile_phone: "",
         company: "",
         trade: "education",
         position: "",
         address3: "",
         company_type: "",
         company_email: "",
         company_phone: "",
         income: "",
         contact: "",
         contact_name: "",
         contact_phone: "",
         contact2: "",
         contact2_name: "",
         contact2_phone: "",
         contact2_dep: "",
         contact2_pos: ""
      },
      resetobj: {},
      verification: false
    };
  },
  methods: {
    Regular() {
      if (!this.form.name || 
          !this.form.identity_card ||
          !this.form.birthday ||
          !this.form.address1 ||
          !this.form.address2 ||
          !this.form.phone ||
          !this.form.mobile_phone ||
          !this.form.company ||
          !this.form.trade || 
          !this.form.contact || 
          !this.form.contact_name || 
          !this.form.contact_phone || 
          !this.form.contact2 || 
          !this.form.contact2_name || 
          !this.form.contact2_phone || 
          !this.form.company_email || 
          !this.form.position || 
          !this.form.address3 || 
          !this.form.company_type || 
          !this.form.company_phone || 
          !this.form.income
        ) {
         this.verification = false;
      } else {
        this.verification = true;
      }
    },
    Submit() {
        this.Regular();
        if (this.verification) {
           LoanInput(this.form)
           .then(res => {
             if (res.data.code == 20000) {
                this.$message({
                  message: '提交成功！',
                  type: 'success'
                });
                this.$router.push("/AppManagement");
             } else {
                this.$message.error('提交出现错误，请检查！');
             }
           })
        } else {
            this.$message({
               message: '您的必填项未填！',
               type: 'warning'
            });
        }
    }
  }
};
</script>
